<template>
  <div class="login">
    <div class="tipslogin">
      <div class="tipsimg">
        <span>登录</span>
        <img src="../../assets/register.gif" alt />
      </div>
      <span @click="returnhome" class="closeSpan">注册</span>
    </div>
    <!--广告图片-->
    <div class="imgDiv">
      <img src="../../assets/music.png" alt class="logo" />
    </div>
    <!--操作区域-->
    <div class="operateDiv">
      <!--账号-->
      <van-cell-group class="file">
        <van-field
          v-model="uname"
          required
          clearable
          label="用户名"
          right-icon="question-o"
          placeholder="请输入用户名"
          @click-right-icon="$toast('question')"
        />

        <van-field v-model="upwd" type="password" label="密码" placeholder="请输入密码" required />
      </van-cell-group>
      <!--登录-->
      <br />
      <van-button size="large" @click="go">登录</van-button>
    </div>
  </div>
</template>

<script>
export default {
  name: "login",
  data() {
    return {
      uname: "",
      upwd: ""
    };
  },
  methods: {
    go() {
      var n = this.uname;
      var p = this.upwd;
      var url = "login";
      var data = `uname=${n}&upwd=${p}`;
      console.log(data);
      this.axios.post(url, data).then(result => {
        console.log(result);
        // 8.并且获取服务器返回结果
        if (result.data.code == 1) {
          console.log(result);
          localStorage.setItem("id", result.data.code);
          // 10.登录成功，返回首页
          this.$messagebox("消息", "登录成功");
          // open("/home","_blank");
          // 自动跳转到某页
          this.$router.push("/my");
          // window.open("cart.vue");
        } else {
          // 9.返回失败，提示
          this.$messagebox("消息", "登录失败");
        }
      });
    },
    returnhome() {
      this.$router.push("/register");
    }
  },
  watch: {}
};
</script>

<style scoped>
/* 你需要登录才能继续仿问的布局样式 */
.tipslogin {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 52px;
  background: rgb(238, 238, 238);
  padding-left: 20px;
  padding-right: 20px;
}
.tipsimg {
  display: flex;
  align-items: center;
}
/* 半闭样式 */
.closeSpan {
  color: rgb(153, 153, 153);
}
/* 图片 logo 根样式 */
.imgDiv {
  display: flex;
  justify-content: center;
}
.imgDiv > img {
  width: 100px;
  height: 100px;
}
/* 操作区域的样式*/
.operateDiv {
  margin-top: 5rem;
}
.file {
  display: flex;
  flex-direction: column;
}
</style>
